import Request from '../../util/luch-request/request.js'
export default {
	data() {
		return {
			cooldownTime:'获取验证码',
			btnDisable:false,
			second:'',
			logUserName:'',
			ResetPassword:'',//重置密码
			ResetPasswordSecond:'',//重置密码重复
			verificationCodeValue:''//验证码
		}
	},
	onLoad() {
		var THAT = this;
		//获取手机信息
		uni.getSystemInfo({
			success: (res) => {
				console.log(res)
				if(res.system.includes("iOS")){
					console.log('IOS')
					THAT.channel = 'IOS';
				}
				else{
					console.log('Android')
					THAT.channel = 'Android';
				}
			}
		})
	},
	methods:{
		//关闭当前页面
		closePage(){
			uni.navigateBack({
			    delta: 1
			});
		},
		//验证码函数
		verificationCode(){
			var THAT = this;
			if(THAT.logUserName){
				const http = new Request();
				let params={
					//+号转义字符 %2B
					phone:'%2B'+'86'+THAT.logUserName,
				}
				http.post('/sms/send', {},{
					params:params,
					header: {
					  'Content-Type': 'application/json;charset=UTF-8'
					},
					dataType: 'json',
					responseType: 'text'
				}).then(res => {
					console.log(params);
					console.log(res);
					if(res.data.ret == 1){
						uni.showToast({
							title:"验证码已发送"
						})
						//倒计时函数
						this.second = 's';
						this.cooldownTime = 60;
						this.btnDisable = true;
						var verTime = setInterval(function(){
							THAT.cooldownTime = THAT.cooldownTime - 1;
							if(THAT.cooldownTime==-1){
								THAT.btnDisable = false;
								THAT.cooldownTime='获取验证码';
								THAT.second='';
								clearInterval(verTime);
							}
						},1000)
					}
					else{
						uni.showToast({
							title:'手机号错误',
							icon:'none'
						})
					}	
					
				}).catch(err => {
					console.log(err);
				})
			}
			else{
				uni.showToast({
					title:"请输入手机号",
					icon:"none"
				})
			}
		},
		// 重置密码
		retrievePassword(){
			let THAT = this;
			if(!THAT.logUserName){
				uni.showToast({
					title:'请输入手机号',
					icon:'none'
				})
			}
			else if(!(THAT.ResetPassword ||THAT.ResetPasswordSecond)){
				uni.showToast({
					title:'请输入密码',
					icon:'none'
				})
			}else if(THAT.ResetPassword!=THAT.ResetPasswordSecond){
				uni.showToast({
					title:'两次输入密码不一致',
					icon:'none'
				})
			}
			else{
				const http = new Request();
				let params={
					//+号转义字符 %2B
					phone:'%2B'+'86'+THAT.logUserName,
					password:'',
					channel:THAT.channel,
					code:THAT.verificationCodeValue,
					type:2
				}
				http.post('/register', {},{
					params:params,
					header: {
					  'Content-Type': 'application/json;charset=UTF-8'
					},
					dataType: 'json',
					responseType: 'text'
				}).then(res => {
					console.log(res);
					//这里接修改密码接口
					let uId = res.data.result.userId
					const http1 = new Request();
					let param={
						id:uId,
						password:THAT.ResetPassword,
						newPassword:THAT.ResetPassword
					}
					http1.post('/register', {},{
						params:param,
						header: {
						  'Content-Type': 'application/json;charset=UTF-8'
						},
						dataType: 'json',
						responseType: 'text'
					}).then(res => {
						console.log(res);
					}).catch(err => {
						console.log(err);
					})
				}).catch(err =>{
					console.log(err);
				})
			}
		}
	}
}
